import React, { Component } from 'react';
import { Container, Row, Col, Jumbotron, Button } from 'reactstrap';

export default class Projects extends Component {
  render() {
    return (
      <div className="Projects section">
		<Container>
			<Row>
				<Col>
					<br/>
					<h2>PROJECTS</h2>
				</Col>
			</Row>
			<Row>
				<Col>
					 <Jumbotron>
						<h2>Strife</h2>
						<h3>Insight</h3>
						<hr className="my-2" />
						<p>Dashboard for discovering gaming insights. Full stack side-project including scraping data from the <a href="https://developer.riotgames.com/">Riot API</a>, munging data, running models and presenting results.</p>
						<p>
						  <Button color="primary" href="/strife">Explore</Button>
						</p>
					  </Jumbotron>
				</Col>
				<Col>
					 <Jumbotron>
						<h2>Politicks</h2>
						<h3>Game Design</h3>
						<hr className="my-2" />
						<p>Form a party, identify your key issues, make backroom deals with minor parties and lead your party to political dominance!</p>
						<p>
						  <Button color="primary" href="https://docs.google.com/document/d/1aEhwUcu31t6kzAqmpsYitgm4rvrA2UWD2BZYhVocBmE/edit?usp=sharing">Explore</Button>
						</p>
					  </Jumbotron>
				</Col>
			</Row>
		</Container>
      </div>
    );
  }
}


